import React from 'react'
import Helmet from 'react-helmet'
import { graphql } from 'gatsby'
import get from 'lodash/get'
import Layout from '../templates/Layout'
import Grid from '@material-ui/core/Grid'
import { withStyles } from '@material-ui/core/styles'
import Typography from '@material-ui/core/Typography'
import PostNavigation from '../molecules/PostNavigation'
import { DiscussionEmbed } from 'disqus-react'
import Paper from '@material-ui/core/Paper'
import Chip from '@material-ui/core/Chip'

const styles = theme => ({
  post: {
    margin: '20px auto',
  },
  chips: {
    display: 'flex',
    justifyContent: 'flex-start',
    flexWrap: 'wrap',
  },
  chip: {
    margin: theme.spacing.unit,
  },
  paper: {
    ...theme.mixins.gutters(),
    paddingTop: theme.spacing.unit * 2,
    paddingBottom: theme.spacing.unit * 2,
  },
  disqus: {
    marginTop: theme.spacing.unit * 2,
  },
})

class BlogPost extends React.Component {
  render() {
    const post = this.props.data.markdownRemark
    const siteTitle = get(this.props, 'data.site.siteMetadata.title')
    const siteDescription = post.excerpt
    const { classes } = this.props
    const disqusShortname = 'pjb0811'
    const disqusConfig = {
      identifier: post.id,
      title: post.frontmatter.title,
    }

    return (
      <Layout location={this.props.location}>
        <Helmet
          htmlAttributes={{ lang: 'en' }}
          meta={[{ name: 'description', content: siteDescription }]}
          title={`${post.frontmatter.title} | ${siteTitle}`}
        />
        <Grid item xs={8} className={classes.post}>
          <Paper className={classes.paper} elevation={1}>
            <Typography variant="h3">{post.frontmatter.title}</Typography>
            <Typography variant="subtitle1" align="right">
              {post.frontmatter.date}
            </Typography>
            <div className={classes.chips}>
              {post.frontmatter.tags.map((tag, i) => (
                <Chip
                  key={i}
                  color="secondary"
                  label={tag}
                  className={classes.chip}
                />
              ))}
            </div>
            <div dangerouslySetInnerHTML={{ __html: post.html }} />
          </Paper>
          <Paper className={`${classes.paper} ${classes.disqus}`} elevation={1}>
            <DiscussionEmbed
              shortname={disqusShortname}
              config={disqusConfig}
            />
          </Paper>
          <PostNavigation {...this.props.pageContext} />
        </Grid>
      </Layout>
    )
  }
}

export default withStyles(styles)(BlogPost)

export const pageQuery = graphql`
  query BlogPostBySlug($slug: String!) {
    site {
      siteMetadata {
        title
        author
      }
    }
    markdownRemark(fields: { slug: { eq: $slug } }) {
      id
      excerpt
      html
      frontmatter {
        title
        date(formatString: "YYYY.MM.DD")
        tags
        mainImage {
          childImageSharp {
            sizes(maxWidth: 1200) {
              aspectRatio
              base64
              sizes
              src
              srcSet
            }
          }
        }
      }
    }
  }
`
